footer {
    background-color: $veryDarkViolet;
    padding: 4rem;
    ul {
        padding: 0;
        li {
            cursor: pointer;
            transition: $mainTransition;
            &:hover {
                color: $cyan;
            }
        }
    }
    .box {
        i {
            &:first-of-type {
                margin-right: 0.75rem;
                @include hoverEffect(back,#1877f2 , $mainTransition);
            }
            &:nth-of-type(2) {
                margin-right: 0.75rem;
                @include hoverEffect(back,#1da1f2 , $mainTransition);
            }
            &:nth-of-type(3) {
                margin-right: 0.75rem;
                @include hoverEffect(back,#004182 , $mainTransition);
            }
            &:nth-of-type(4) {
                @include hoverEffect(back,#ff0000 , $mainTransition);
            }
        }
    }
    @include maxMobil {
        text-align: center;
    }
}