////////////////////////////
// This is min media 
@mixin minSmall {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin minMedium {
    @media (min-width: 992px) {
        @content;
    }
}
@mixin minLarge {
    @media (min-width: 1200px) {
        @content;
    }
}
@mixin maxMobil {
    @media (max-width: 767px) {
        @content;
    }
}
@mixin maxMedium {
    @media (max-width: 991px) {
        @content;
    }
}
@mixin maxLarge {
    @media (max-width: 1999px) {
        @content;
    }
}



///////////////////////////////
// Overlays
@mixin overlayMiddle($background-color,$overlay-transition) {
    position: relative;
    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 0;
        height: 0;
        background-color: $background-color;
        transition: #{$overlay-transition}s;
    }
    &:hover::before {
        width: 100%;
        height: 100%;
    }
}






///////////////////////////////
// Buttons
@mixin buttonWithHover($color,$backColor , $transition, $padX:0.75rem, $padY: 0.5rem , $borRad: 1rem) {
    color: $color;
    background-color: rgba($backColor ,0.8);
    padding: $padY $padX;
    transition: $transition;
    cursor: pointer;
    user-select: none;
    font-weight: bold;
    border-radius:  $borRad;
    &:hover,
    &:focus {
        background-color: rgba($backColor ,1);
    }
}
@mixin hoverEffect($type: back,$color , $transition) {
    @if ($type == back) {
        background-color: rgba($color , 0.6) !important;
        transition: $transition;
        &:hover ,
        &:focus  {
            background-color: rgba($color ,1) !important;
        }
    }@else {
        color: rgba($color , 0.6) !important;
        transition: $transition;
        &:hover ,
        &:focus  {
            color: rgba($color ,1) !important;
        }
    }

}


///////////////////////////////
// Border

@mixin border($where , $width , $color) {
    @if ($where == all) {
        border: $width solid $color;
    } @else if ($where == x) {
        border-left: $width solid $color;
        border-right: $width solid $color;
    } @else if ($where == y) {
        border-top: $width solid $color;
        border-bottom: $width solid $color;
    } @else {
        border-#{$where}: $width solid $color;
    }
}

///////////////////////////////
// Input
@mixin input($padX , $padY , $backColor: white ,$color: gray ,$borRad , $borWhere ,$borWid , $borCol , $transition) {
    padding: $padY $padX;
    border-radius: $borRad;
    outline: none;
    border: none;
    @include border($borWhere , $borWid , $borCol);
    background-color: $backColor;
    color: $color;
    &::placeholder {
        color: $color;
        transition: $transition;
    }
    &:focus::placeholder {
        opacity: 0;
    }
}




///////////////////////////////
// Flex-Pack
@mixin flexPack($jus , $ali) {
    display: flex;
    justify-content: #{$jus};
    align-items: #{$ali};
}





