header{
    .container {
        .text {
            transition: $mainTransition;
            &.op-1 {
                opacity: 1;
            }
            ul {
                margin-bottom: 0;
                li {
                    a {
                        text-decoration: none;
                        padding: 0.25rem 0.5rem;
                        @include hoverEffect(font,#343131 , $mainTransition);
                    }
                }
            }
            .logIn  {
                span:first-of-type {
                    @include hoverEffect(font,#343131 , $mainTransition);
                }
            }
        }
        @include maxMobil {
            justify-content: space-between;
            .text {
                border-radius: 15px;
                flex-flow: column;
                opacity: 0;
                text-align: left !important;
                width: 75%;
                background-color: $darViolet;
                position: absolute;
                right: 5px;
                top: 100%;
                padding: 1rem 3rem 1.5rem;
                align-items: center;
                &::after {
                    content: "";
                    position: absolute;
                    top: -28px;
                    height: 0;
                    width: 0;
                    border: 15px solid;
                    border-color: transparent transparent $darViolet transparent;
                    right: 10px;
                    transition: $mainTransition;
                }
                ul {
                    flex-flow: column;
                    padding: 0;
                    li {
                        a {
                            display: inline-flex;
                            padding: 1rem;
                            width: 100%;
                            @include hoverEffect(font,white, $mainTransition);
                        }
                    }
                }
                .logIn  {
                    flex-flow: column;
                    span:first-of-type {
                        padding: 1rem;
                        display: block;
                        @include hoverEffect(font,white , $mainTransition);
                    }
                    span:nth-of-type(2) {
                        display: block;
                        width: 100%;
                        padding: {
                            left: 4rem;
                            right: 4rem;
                        }
                    }
                }
            }
        }
    @include maxMobil {
        .bars {
            width: 30px;
            height: 30px;
            cursor: pointer;
            span {
                display: block;
                background-color: #343131;
                width: 100%;
                height: 4px;
                margin-bottom: 5px;
                border-radius: 5px;
            }
        }
    }
    }
}