.main-button {
    @include buttonWithHover(white , $cyan , $mainTransition , 0.85rem , 0.65rem , 2em);
}
.main-input {
    @include input(2rem , 1rem , #fff , black , 0 , all , 1px , $grayishViolet , $mainTransition )
}
.f-center {
    @include flexPack(center , center);
}
.f-between {
    @include flexPack(space-between , center);
}