.home {
    .container {
        text-align: center;
        .text { 
            order: 2;
            margin-top: 8rem;
            h1 {
                color: $veryDarkBlue;
            }
        }
        .img-box{
            order: 1;
            img {
                margin: 3rem auto;
                
            }
        }
    }
    @include minLarge {
        .container {
            text-align: left;
            flex-flow: row-reverse !important;
            .img-box{
                transform: translateX(30%);
            }
        }
    }
}
.links {
    padding-top: 10rem;
    .container {
        .bottom {
            form {
                input.error {
                    @include border(all , 0.2rem ,rgba(red , 0.8));
                    &::placeholder {
                        color: rgba(red , 0.8);
                    }
                }
            }
            background: top no-repeat url("../img/bg-boost-desktop.svg");
            background-color: $darViolet;
            position: absolute;
            left: 0;
            bottom: -4.5rem;
            width: 100%;
            @include maxMobil {
                bottom: -7rem;
                form{
                    flex-flow: column;
                }
            }
        }
    }
}
.stats {
    padding-top: 10rem;
    padding-bottom: 5rem;
    background-color: $grayishViolet;
    .text {
        margin: 2rem auto 8em;
        h1 {
            color: $veryDarkBlue;
        }
    }
    .stats-cont {
        gap: 30px;
        display: flex;
        flex-flow: column;
        .stats-box {
            z-index: 3;
            .img-cont {
                top: -40px;
                left: 30px;
                background-color: $darViolet;
            }
        }
        @include maxMobil {
            width: 20rem !important;
        }
        @include maxLarge {
            margin: auto;
            width: 35rem;
            gap: 80px;
            .stats-box {
                .img-cont {
                    left: 50%;
                    transform: translateX(-50%);
                }
                &:not(:last-of-type):after {
                    content: "";
                    bottom: -75px;
                    left: 50%;
                    background-color: $cyan;
                    transform: translateX(-50%);
                    position: absolute;
                    width: 15px;
                    height: 75px;
                    z-index: 1;
                }
            }
        }
        @include minLarge {
            width: auto;
            display: grid;
            grid-template-columns: repeat(auto-fill , minmax(300px , 1fr)) !important;
            padding-bottom: 10rem;
            .stats-box {
                &:nth-of-type(2) {
                    transform: translateY(20%);
                }
                &:nth-of-type(3) {
                    transform: translateY(40%);
                }
                &:not(:last-of-type):after {
                    bottom: 0;
                    top: 70%;
                    left: 100%;
                    background-color: $cyan;
                    transform: translateY(-50%);
                    position: absolute;
                    width: 80px;
                    height: 15px;
                    z-index: 1;
                }
                &:nth-of-type(2)::after {
                    top: 50%;
                }
            }
        }
    }
}
.boost {
    background: top no-repeat url("../img/bg-boost-desktop.svg");
    background-color: $darViolet;
}